/* eslint-disable eqeqeq */
/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';

import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import IntlMessages from '../../../helpers/IntlMessages';

import { adminRoot } from '../../../constants/defaultValues';
import {
  getAllHackathons,
  updateCurrentHackathon,
} from '../../../redux/actions';
import { NotificationManager } from '../../../components/common/react-notifications';
import useRequest from '../../../hooks/use-request';
import { wait } from '../../../helpers/Utils';

const Basics = ({
  match,
  accessToken,
  updateAction,
  currentHackathon,
  refresh,
  loading,
}) => {
  const { id } = match.params;

  const history = useHistory();

  if (!id) {
    history.goBack();
  }

  useEffect(() => {
    updateAction(id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const {
    name,
    tagline,
    about,
    aproximate_participants,
    max_team_size,
    venue,
  } = currentHackathon;
  console.log(currentHackathon);
  const [values, setValues] = useState(() => ({
    name,
    venue,
    tagline,
    about,
    aproximate_participants,
    max_team_size,
  }));

  useEffect(() => {
    setValues({
      name: currentHackathon.name,
      venue: currentHackathon.venue || '',
      tagline: currentHackathon.tagline || '',
      about: currentHackathon.about || '',
      aproximate_participants: currentHackathon.aproximate_participants,
      max_team_size: currentHackathon.max_team_size,
    });
  }, [currentHackathon]);

  const handleChange = (event, key) => {
    setValues({
      ...values,
      [key]: event.target.value,
    });
  };

  const [doRequest, requestLoading] = useRequest({
    url: '/updateHackathon',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: { id, ...values },
    nextSuccess: () => {
      NotificationManager.success(
        'Hackathon updated successfully!',
        'Hackathon Operation Success',
        3000,
        null,
        null,
        ''
      );
      refresh(accessToken, id);
      wait(1200, () => {
        history.push(`${adminRoot}/challenges/links/${id}`);
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Hackathon Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleSubmit = () => {
    if (
      values.name === '' ||
      values.tagline === '' ||
      values.about === '' ||
      values.aproximate_participants == 0 ||
      values.max_team_size == 0
    ) {
      NotificationManager.error(
        'Please enter all the information!',
        'Hackathon Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  if (loading) return <div className="loading" />;

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.basics" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="8" className="mb-4">
          <Card className="mb-4">
            <CardBody>
              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.hackathon-name" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="hackathon-name"
                  value={values.name}
                  onChange={(event) => {
                    handleChange(event, 'name');
                  }}
                />
              </FormGroup>
              
              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="Venue" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="hackathon-venue"
                  value={values.venue}
                  placeholder = "Fill if it's a Offline Hackathon"
                  onChange={(event) => {
                    handleChange(event, 'venue');
                  }}
                />
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.hackathon-tagline" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="hackathon-tagline"
                  value={values.tagline}
                  onChange={(event) => {
                    handleChange(event, 'tagline');
                  }}
                />
              </FormGroup>

              <FormGroup>
                <Label>
                  <IntlMessages id="challenges.about" />
                </Label>
                <Input
                  type="textarea"
                  className="form-control"
                  name="about"
                  rows="6"
                  value={values.about}
                  onChange={(event) => {
                    handleChange(event, 'about');
                  }}
                />
              </FormGroup>

              <FormGroup>
                <Label>
                  <IntlMessages id="challenges.aproximate-participants" />
                </Label>
                <Input
                  type="number"
                  className="form-control"
                  name="aproximate-participants"
                  value={values.aproximate_participants}
                  onChange={(event) => {
                    handleChange(event, 'aproximate_participants');
                  }}
                />
              </FormGroup>

              <FormGroup>
                <Label>
                  <IntlMessages id="challenges.max-team-size" />
                </Label>
                <Input
                  type="number"
                  className="form-control"
                  name="max-team-size"
                  value={values.max_team_size}
                  onChange={(event) => {
                    handleChange(event, 'max_team_size');
                  }}
                />
              </FormGroup>
            </CardBody>
          </Card>

          <FormGroup className="text-center">
            <Button
              id="submit-basics"
              type="submit"
              color="primary"
              className={`btn-shadow btn-multiple-state ${
                requestLoading ? 'show-spinner' : ''
              }`}
              onClick={handleSubmit}
            >
              <span className="spinner d-inline-block">
                <span className="bounce1" />
                <span className="bounce2" />
                <span className="bounce3" />
              </span>
              <span className="label">
                <IntlMessages id="forms.update" />
              </span>
            </Button>
          </FormGroup>
        </Colxx>
      </Row>

    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { currentHackathon, loading, error } = hackathonData;
  return {
    accessToken,
    currentHackathon,
    loading,
    error,
  };
};

export default connect(mapStateToProps, {
  updateAction: updateCurrentHackathon,
  refresh: getAllHackathons,
})(Basics);
